<template>
  <v-app-bar app dense fixed>
    <!-- Menu button. -->
    <v-app-bar-nav-icon
      @click="setOpenPanelServiceList(!openPanelServiceList)"
    ></v-app-bar-nav-icon>
    <v-spacer></v-spacer>
    <!-- Light or dark theme button. -->
    <v-tooltip bottom>
      <template v-slot:activator="{ on, attrs }">
        <v-btn icon v-bind="attrs" v-on="on" @click="changeTheme()">
          <v-icon>mdi-brightness-4</v-icon>
        </v-btn>
      </template>
      <span>{{ $t("message.1") }}</span>
    </v-tooltip>
    <v-spacer></v-spacer>
    <!-- Logout button. -->
    <v-tooltip bottom>
      <template v-slot:activator="{ on, attrs }">
        <v-btn icon v-bind="attrs" v-on="on" @click="logout()">
          <v-icon>mdi-logout</v-icon>
        </v-btn>
      </template>
      <span>{{ $t("message.2") }}</span>
    </v-tooltip>
  </v-app-bar>
</template>

<script>
import { mapState, mapMutations } from "vuex";

export default {
  name: "AppBar",

  computed: {
    ...mapState(["openPanelServiceList"]),
  },

  methods: {
    ...mapMutations(["setIsAuthenticated", "setOpenPanelServiceList"]),
    ...mapMutations("popUpMsgs", ["setShowMsg"]),
    logout() {
      this.setShowMsg(false);
      this.axios
        .post("/admin/logout", {})
        .then(() => {
          this.setIsAuthenticated(false);
        })
        .catch((error) => {
          console.log(error);
        });
    },
    changeTheme() {
      this.$vuetify.theme.dark = !this.$vuetify.theme.dark;
    },
  },
};
</script>
