<template>
  <v-app>
    <template v-if="isAuthenticated">
      <NavigationBar />
      <AppBar />
      <PopUpMsgs />
      <PageLockout />
      <v-main>
        <router-view />
      </v-main>
    </template>
    <template v-else>
      <router-view v-if="$route.name === 'signin'" />
    </template>
  </v-app>
</template>

<script>
import { mapState, mapMutations, mapActions } from "vuex";
import NavigationBar from "@/components/NavigationBar.vue";
import AppBar from "@/components/AppBar.vue";
import PopUpMsgs from "@/components/PopUpMsgs.vue";
import PageLockout from "@/components/PageLockout.vue";

export default {
  name: "App",

  components: {
    NavigationBar,
    AppBar,
    PopUpMsgs,
    PageLockout,
  },

  computed: {
    ...mapState(["brand", "slogan", "isAuthenticated", "serviceList"]),
  },

  watch: {
    isAuthenticated: function (flag) {
      if (flag) {
        // Get a list of services and collections
        if (this.serviceList.length === 0) {
          this.getServiceList();
        }
        // to home page
        this.$router.push({ name: "home" });
      } else {
        this.setOpenPanelServiceList(false);
        this.setCurrentPageNumber(1);
        this.setProgressionStep(1);
        this.setDocsPerPage(50);
        this.setSelectedService([]);
        this.setServiceList([]);
        this.$router.push({ name: "signin" });
      }
    },
  },

  methods: {
    ...mapMutations([
      "setBrand",
      "setSlogan",
      "setIsAuthenticated",
      "setSelectedService",
      "setServiceList",
      "setOpenPanelServiceList",
    ]),
    ...mapMutations("ckeditor", ["setLanguageCKEditor"]),
    ...mapMutations("documentList", [
      "setCurrentPageNumber",
      "setProgressionStep",
      "setDocsPerPage",
    ]),
    ...mapActions(["ajaxGetLangAndParameters"]),
    ...mapActions("popUpMsgs", ["runShowMsg"]),
    // Get a list of services and collections
    getServiceList() {
      if (this.isAuthenticated) {
        this.axios
          .post("/admin/service-list")
          .then((response) => {
            const data = response.data;
            if (!data.is_authenticated) {
              this.setIsAuthenticated(false);
              return;
            }
            const listLength = data.service_list.length;
            if (listLength > 0) {
              const selectedServiceList = [];
              for (let idx = 0; idx < listLength; idx++) {
                selectedServiceList.push(undefined);
              }
              this.setSelectedService(selectedServiceList);
              this.setServiceList(data.service_list);
            } else {
              console.log("No data available");
              this.runShowMsg({
                text: "No data available",
                status: "warning",
              });
            }
          })
          .catch((error) => {
            console.log(error);
          });
      }
    },
  },

  created() {
    // Get language code and parameters for admin panal.
    this.axios
      .get("/admin/get-parameters")
      .then((response) => {
        const data = response.data;
        // locale
        if (data.lang_code.length > 0) {
          this.$i18n.locale = data.lang_code;
        } else {
          this.$i18n.locale = this.$userLang;
        }
        this.setLanguageCKEditor(this.$i18n.locale);
        // theme
        this.$vuetify.theme.dark = data.dark_theme;
        // primary colors
        this.$vuetify.theme.themes.light.primary = data.light_color_primary;
        this.$vuetify.theme.themes.dark.primary = data.dark_color_primary;
        // brand and slogan
        this.setBrand(data.brand);
        this.setSlogan(data.slogan);
        //
        this.setIsAuthenticated(data.is_authenticated);
      })
      .catch((error) => {
        window.console.log(error);
        this.runShowMsg({
          text: String(error),
          status: "error",
        });
      });
  },
};
</script>

<style>
.panel-width-service-list__slider .v-slider {
  max-width: 195px;
}
.service-list__icon {
  max-width: 37px;
}
</style>
