import Vue from "vue";
import VueRouter from "vue-router";
import HomePage from "../views/HomePage.vue";

Vue.use(VueRouter);

const routes = [
  // Home page.
  {
    path: "/",
    name: "home",
    component: HomePage,
  },
  // Login page.
  {
    path: "/sign-in",
    name: "signin",
    component: () => import("../views/LoginPage.vue"),
  },
  // Document list
  {
    path: "/:service/:indexService/:collection/:indexCollection/document-list",
    name: "documentList",
    component: () => import("../views/DocumentList.vue"),
  },
  // Open a document in a form.
  {
    path: "/:service/:indexService/:collection/:indexCollection/document/:indexDoc",
    name: "documenForm",
    component: () => import("../views/DocumentForm.vue"),
  },
  // Error 404 - Page not found.
  {
    path: "*",
    name: "notFound",
    component: () => import("../views/NotFound.vue"),
  },
];

const router = new VueRouter({
  mode: "history",
  base: process.env.NODE_ENV === "production" ? "/admin/" : "/",
  routes,
});

export default router;
